:root {
  --bs-body-font-family: Inter, system-ui, -apple-system, Segoe UI, Roboto, Helvetica Neue, Noto Sans, Liberation Sans, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
  --bs-body-color: rgb(156, 163, 175);
  --bs-border-color: rgba(229, 231, 235, 0.164);
  --bs-border-color-translucent: rgba(229, 231, 235, 0.164);
}

.navbar {
  --bs-navbar-color: rgb(156, 163, 175);
  --bs-navbar-hover-color: #d9d9d9;
  --bs-navbar-disabled-color: #999999;
  --bs-navbar-active-color: #b3b3b3;
  --bs-navbar-brand-color: rgb(255, 255, 255);
  --bs-navbar-brand-hover-color: #d9d9d9;
}

.card {
  --bs-card-bg: rgb(35, 45, 59);
}

.btn {
  background: linear-gradient(to bottom right, rgb(79, 70, 229), rgb(120, 51, 234));
}

.btn-primary {
  --bs-btn-color: rgb(255, 255, 255);
  --bs-btn-border-color: rgb(79, 70, 229);
  --bs-btn-hover-color: #d9d9d9;
  --bs-btn-hover-bg: #251bc3;
  --bs-btn-hover-border-color: #251bc3;
  --bs-btn-focus-shadow-rgb: 49,132,253;
  --bs-btn-active-color: #b3b3b3;
  --bs-btn-active-bg: #181280;
  --bs-btn-active-border-color: rgb(79, 70, 229);
  --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  --bs-btn-disabled-color: #999999;
  --bs-btn-disabled-bg: #100c53;
  --bs-btn-disabled-border-color: #100c53;
}